button {
    height: em(50);
    border: 0;
    cursor: pointer;
}

.btn-danger {
    @include button-base-configuration;
    background-color: $danger;
    color: $white;
}

.btn-cancel {
    @include button-base-configuration;
    background-color: $light;
}

.icon-warn-container {
    background-color: $danger;
    border-radius: 25px;
    height: 50px;
    width: 50px;
    text-align: center;
    &:hover {
        background-color: $white;
        border: 1px solid $danger;
    }
}

.btn-filter {
    border-radius: 50%;
    height: 35px;
    width: 35px;
    color: white;
    &.dark {
        background-color: purple;
    }
    &.water {    
        background-color: blue;
    }
    &.earth {
        background-color: green;
    }
    &.storm {
        color: black;
        background-color: yellow;
    }
    &.light {
        background-color: silver;
        color: black;
    }
    &.fire {
        background-color: red;
    }
}

.icon-success-container {
    background-color: $success;
    border-radius: 25px;
    height: 50px;
    width: 50px;
    text-align: center;
    &:hover {
        background-color: $white;
        border: 1px solid $success;
    }
}